"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import type { Book, Author } from "@/lib/baserow/types"
import { Button } from "@/components/ui/button"
import { Card, CardContent } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { BookOpen, Calendar, User, ArrowLeft, CheckCircle, AlertCircle, Ban } from "lucide-react"
import { useAppSelector, useAppDispatch } from "@/lib/redux/hooks"
import { selectIsAuthenticated, selectUser } from "@/lib/redux/slices/authSlice"
import { useBorrowBookMutation, useGetBorrowedBooksQuery, baserowApi } from "@/lib/redux/services/baserowApi"
import { toast } from "sonner"
// Assuming sonner or similar is used, or fallback to local state. 
// The original code used a local message state. I will keep using it or verify if 'sonner' is available. 
// Package.json has "sonner": "^1.7.4". I'll use it for better UX, or stick to existing UI for minimal diff.
// Sticking to existing UI for now to reduce scope creep, but using mutation state.

interface BookDetailsProps {
  book: Book
  isLoggedIn: boolean
  hasBorrowed: boolean
  userId?: string
  borrowedCountInPeriod: number
  maxBorrowsPerPeriod: number
}

export function BookDetails({
  book,
}: { book: Book }) {
  const router = useRouter()
  const dispatch = useAppDispatch()

  // Hydrate cache with server-side data
  useEffect(() => {
    if (book) {
      dispatch(baserowApi.util.upsertQueryData('getBook', book.id, book))
    }
  }, [dispatch, book])

  const isAuthenticated = useAppSelector(selectIsAuthenticated)
  const user = useAppSelector(selectUser)

  const [borrowBook, { isLoading: isBorrowing }] = useBorrowBookMutation()
  const { data: borrowedBooks } = useGetBorrowedBooksQuery(user?.id || "", {
    skip: !user?.id
  })

  // Calculate stats from client cache
  const hasBorrowed = borrowedBooks?.some(b => b.book_id === book.id)
  // Logic for "borrowedCountInPeriod" - assuming checking active borrows for now or filtered by date
  // simplified logic: count "borrowed" status
  const currentBorrows = borrowedBooks?.filter(b => b.status === "borrowed").length || 0
  const maxBorrowsPerPeriod = 2 // Hardcoded for now or fetch from config

  const [message, setMessage] = useState<{
    type: "success" | "error"
    text: string
  } | null>(null)

  const hasReachedBorrowLimit = currentBorrows >= maxBorrowsPerPeriod

  const handleBorrow = async () => {
    if (!isAuthenticated || !user) {
      router.push("/auth/login") // Or use login modal logic if exists
      // Ideally router.push("/login") if that's the page
      return
    }

    if (user.isAdmin) {
      setMessage({
        type: "error",
        text: "Administrators cannot borrow books.",
      })
      return
    }

    if (hasReachedBorrowLimit) {
      setMessage({
        type: "error",
        text: `You have reached the limit of ${maxBorrowsPerPeriod} books.`,
      })
      return
    }

    setMessage(null)

    console.log("Attempting to borrow:", { bookId: book.id, userId: user?.id })
    console.log("DEBUG: Full User Object:", user)
    console.log("DEBUG: Full Book Object:", book)

    if (!book.id || !user?.id) {
      setMessage({
        type: "error",
        text: `Missing info. BookID: ${book.id}, UserID: ${user.id}`
      })
      return
    }

    try {
      const result = await borrowBook({
        bookId: book.id,
        userId: user.id,
        dueDate: new Date(Date.now() + 14 * 24 * 60 * 60 * 1000).toISOString()
      }).unwrap()

      setMessage({ type: "success", text: "Book borrowed successfully!" })
      // No need to router.refresh() if cache invalidation works!
    } catch (err) {
      setMessage({ type: "error", text: "Failed to borrow book. Please try again." })
    }
  }

  const renderBorrowAction = () => {
    if (user?.isAdmin) {
      return (
        <Badge variant="secondary" className="text-muted-foreground">
          <Ban className="h-4 w-4 mr-1.5" />
          Admins Cannot Borrow
        </Badge>
      )
    }

    if (hasBorrowed) {
      return (
        <Badge variant="outline" className="text-primary border-primary">
          <CheckCircle className="h-4 w-4 mr-1.5" />
          Currently Borrowed
        </Badge>
      )
    }

    if (hasReachedBorrowLimit) {
      return (
        <Badge variant="secondary" className="text-muted-foreground">
          <Ban className="h-4 w-4 mr-1.5" />
          Borrow Limit Reached
        </Badge>
      )
    }

    if (book.book_id) {
      return (
        <Button onClick={handleBorrow} disabled={isBorrowing}>
          {isBorrowing ? "Borrowing..." : "Borrow Book"}
        </Button>
      )
    }

    return (
      <Badge variant="destructive">
        <AlertCircle className="h-4 w-4 mr-1.5" />
        Not Available
      </Badge>
    )
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <Button variant="ghost" asChild className="mb-6">
        <Link href="/books">
          <ArrowLeft className="h-4 w-4 mr-2" />
          Back to Books
        </Link>
      </Button>

      <div className="grid md:grid-cols-3 gap-8">
        <div className="md:col-span-1">
          <Card className="overflow-hidden border-border">
            <div className="aspect-[3/4] bg-gradient-to-br from-primary/20 to-primary/5 flex items-center justify-center">
              <BookOpen className="h-24 w-24 text-primary/40" />
            </div>
          </Card>
        </div>

        <div className="md:col-span-2 space-y-6">
          <div>
            <h1 className="text-3xl font-bold text-foreground mb-4">{book.title}</h1>
            <h3 className="text-lg font-semibold text-foreground flex items-center gap-2"><User className="h-5 w-5" />{book.author_name}</h3>
            {/* {book.author && (
              <Link
                href={`/authors/${book.author.id}`}
                className="flex items-center gap-2 text-lg text-primary hover:underline"
              >
                <User className="h-5 w-5" />
                {book.author.name}
              </Link>
            )} */}
          </div>

          <div className="flex flex-wrap gap-3">
            {book.published_year && (
              <Badge variant="outline" className="text-sm">
                <Calendar className="h-3.5 w-3.5 mr-1.5" />
                {book.published_year}
              </Badge>
            )}
          </div>

          <Card className="border-border">
            <CardContent className="p-4">
              <h2 className="font-semibold mb-2 text-foreground">Description</h2>
              <p className="text-muted-foreground leading-relaxed">{book.description || "No description available."}</p>
            </CardContent>
          </Card>

          {isAuthenticated && (
            <Card className="border-border">
              <CardContent className="p-4">
                <h2 className="font-semibold mb-2 text-foreground">Your Borrowing Status</h2>
                <p className="text-sm text-muted-foreground">
                  Books borrowed:{" "}
                  <span className="font-medium text-foreground">{currentBorrows}</span> / {maxBorrowsPerPeriod}
                </p>
                <div className="w-48 h-2 bg-secondary rounded-full mt-2">
                  <div
                    className={`h-2 rounded-full transition-all ${hasReachedBorrowLimit ? "bg-destructive" : "bg-primary"}`}
                    style={{
                      width: `${(currentBorrows / maxBorrowsPerPeriod) * 100}%`,
                    }}
                  />
                </div>
                {hasReachedBorrowLimit && (
                  <p className="text-xs text-destructive mt-2">
                    You have reached your borrowing limit. Please wait or return a book.
                  </p>
                )}
              </CardContent>
            </Card>
          )}

          <Card className="border-border">
            <CardContent className="p-4">
              <h2 className="font-semibold mb-3 text-foreground">Availability</h2>
              <div className="flex items-center justify-between">
                <div>
                  {/* <p className="text-sm text-muted-foreground">
                    {book.available_copies} of {book.total_copies} copies available
                  </p> */}
                  {/* <div className="w-48 h-2 bg-secondary rounded-full mt-2">
                    <div
                      className="h-2 bg-primary rounded-full transition-all"
                      style={{
                        width: `${(book.available_copies / book.total_copies) * 100}%`,
                      }}
                    />
                  </div> */}
                </div>

                {renderBorrowAction()}
              </div>

              {message && (
                <div
                  className={`mt-4 p-3 rounded-lg text-sm ${message.type === "success" ? "bg-primary/10 text-primary" : "bg-destructive/10 text-destructive"
                    }`}
                >
                  {message.text}
                </div>
              )}
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
